#welcome-section{
    background-repeat: no-repeat;
    background-size:cover;
    background-position:center;
    background-image:linear-gradient(
        rgba(250, 250 ,250, .3), 
        rgba(250, 250 ,250, .1)),
        url("../images/pankaj-patel-1073203-unsplash.jpg");
    color: white;
    min-height: 100vh;
    overflow: hidden;
    position: relative;
    padding-bottom: 100px;
    text-align: center;
    margin-top: 50px;
    /* margin-bottom: 50px; */
  }
  
  #welcome-section h1{
    margin-top: 40px;
    margin-bottom: 15px;
    background: #222;
    padding: 10px;
    display: inline-block;
    margin-left: 20%;
    margin-right: 20%;
  }

  #welcome-section p{
    margin-left: 20%;
    margin-right: 20%;
    margin-bottom: 15px;
    background: #222;
    padding: 10px;
    display: inline-block;
    font-size: 1.5rem;
  }

  #skills span{
    margin-right: 10px;
    margin-bottom: 10px;
    /* margin-bottom: 15px; */
    background: #222;
    padding: 10px;
    display: inline-block;
    font-size: 1.5rem;
  }
  #skills{
    display: block;
  }

  @media(max-width: 700px){
    #welcome-section{
      min-height: 100vh;
    }
    #welcome-section h1{
      margin-top: 70px;
    }
  }